@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@800&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Manrope";
}

body .app{
    width: 1440px;
    height: 800px;
    display: grid;
    align-items: center;
    justify-content: center;
    background-color: hsl(218, 23%, 16%);
}

@media screen and (max-width:400px) {
    body .app{
        width: 375px;
        height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: hsl(218, 23%, 16%);
    }
}